import { createContext, useContext, useState, useEffect } from 'react';
import { useCookies } from 'react-cookie';
import {
  Box,
  Alert, AlertTitle, Slide,
  Backdrop, CircularProgress,
} from '@mui/material';



const AuthContext = createContext();

export function AuthProvider({ children }) {
  const [isAuth, setIsAuth] = useState(false);
  const [isRunning, setIsRunning] = useState(false);
  const [userName, setUserName] = useState('');
  const [message, setMessage] = useState('まいたけ帝国へようこそ！');
  const [severity, setSeverity] = useState('info');
  const [alertTimerId, setAlertTimerId] = useState();

  const [cookies, setCookie, removeCookie] = useCookies(['IS_ACCEPT_ALL_COOKIE', 'JWT_TOKEN']);

  const url = 'https://script.google.com/macros/s/AKfycbx5r8BXN0jcUYk3exP8_tRbuL8THNjcrHXZYmTQourSneCq4SGVprtjeC80NLtnZZp9pQ/exec';



  const translatedMessage = (message) => {
    switch (message) {
      // MaitakeAuth
      case 'auth request error':
        return '認証リクエストに失敗したよ。管理者に問い合わせてね。';
      // SignUpAndSignIn
      case 'request error':
        return 'リクエストに失敗したよ。もう一度やり直してね。';
      case 'invalid user':
        return '無効なトークンだよ。サインインし直してください。';
      // サインアップ
      case 'invalid user name':
        return 'このあだ名は使えないよ。１～１２文字のアルファベットにしてね。';
      case 'invalid user password':
        return 'この秘密の暗号は使えないよ。３文字のアルファベットにしてね。';
      case 'sign up error':
        return 'サインアップに失敗したよ。もう一度やり直してね。';
      case 'sign up succeeded':
        return `サインアップに成功したよ！ようこそ、${userName}さん！`;
      case 'already in use':
        return 'このあだ名は既に使われているよ。';
      // サインイン
      case 'user name not found':
        return 'このあだ名は見つからないよ。';
      case 'sign in succeeded':
        return `サインインに成功したよ！ようこそ、${userName}さん！`;
      case 'sign in failed':
        return 'サインインに失敗したよ。もう一度やり直してね。';
      // 認証
      case 'signature error':
        return '署名が使えないよ。サインアウトして、もう一度サインインしてね。';
      case 'sheet id error':
        return 'データベースでエラーが発生したよ。管理者に問い合わせてね。';
      case 'invalid token user name':
        return 'トークンのあだ名が使えないよ。サインアウトして、もう一度サインインしてね。';
      case 'token expired':
        return 'トークンが有効期限を過ぎているよ。サインアウトして、もう一度サインインしてね。';
      case 'token updated':
        return `トークンを更新したよ！ようこそ、${userName}さん！`;
      case 'verify JWT succeeded':
        return `トークンの認証に成功したよ！ようこそ、${userName}さん！`;
      case 'invalid token':
        return '無効なトークンだよ。サインアウトして、もう一度サインインしてね。';

      default:
        return message;
    }
  }

  const saveCookieJwtToken = (data) => {

    setSeverity(data.result ? 'success' : 'warning');
    setMessage(translatedMessage(data.message));

    // サインインに失敗またはCookieのIS_ACCEPT_ALL_COOKIEがfalseなら、終了
    if (!(cookies.IS_ACCEPT_ALL_COOKIE && data.result)) {
      setIsAuth(false);
      return;
    }

    try {
      // トークンを「.」で分割してヘッダー、ペイロード、署名部分に分割
      const payloadBase64 = data.jwtToken.split('.')[1];
      // ペイロードをBase64デコードしてオブジェクトに変換
      const payload = JSON.parse(atob(payloadBase64));
      // UnixタイムスタンプをJavaScriptの日時に変換、30日後に変換
      const cookieExpiration = new Date(payload.exp * 1000 + 3600 * 24 * 30);
      setCookie('JWT_TOKEN', data.jwtToken, { path: '/', expires: cookieExpiration, secure: true, sameSite: 'strict' });
      setUserName(payload.sub);
      setIsAuth(true);
    } catch (e) {
      setIsAuth(false);

      setSeverity('error');
      setMessage(e.message);
    }
  }



  const authSignUpAndSignIn = async (formData) => {
    setMessage('');

    const options = {
      method: 'POST',
      headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
      body: `action=${formData.action}&userName=${formData.userName}&userPassword=${formData.userPassword}`
    }
    await fetch(url, options)
      .then(res => res.json())
      .then(data => {
        saveCookieJwtToken(data);
      }).catch(e => {
        setIsAuth(false);

        setSeverity('error');
        setMessage(e.message);
      })
  }



  const authSignOut = () => {
    removeCookie('JWT_TOKEN', { path: '/' });
    setIsAuth(false);
    setUserName('');

    setSeverity('success');
    setMessage('サインアウトに成功したよ！またね！');
  }



  const authVerifyJwt = async () => {
    // CookieにJWT_TOKENが保存されていなかったら、終了
    if (cookies.JWT_TOKEN == null || cookies.JWT_TOKEN === 'undefined') {
      setIsAuth(false);
      return;
    }

    const options = {
      method: 'POST',
      headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
      body: `action=verifyJwt&jwtToken=${cookies.JWT_TOKEN}`
    }
    await fetch(url, options)
      .then(res => res.json())
      .then(data => {
        saveCookieJwtToken(data);
      }).catch(e => {
        setIsAuth(false);

        setSeverity('error');
        setMessage(e.message);
      })
  }



  // アラートを5秒後に閉じる
  useEffect(() => {
    if (!!alertTimerId || !message) {
      clearTimeout(alertTimerId);
    }
    if (!!message) {
      const timer = setTimeout(() => {
        setMessage('');
      }, 5000);
      setAlertTimerId(timer);

      return () => {
        clearTimeout(timer);
        setAlertTimerId('');
      }
    }
  }, [message]);



  return (
    <AuthContext.Provider value={{ isAuth, isRunning, setIsRunning, cookies, setCookie, removeCookie, authSignUpAndSignIn, authSignOut, authVerifyJwt }}>
      <Box
        style={{
          position: 'fixed',
          top: 0,
          left: 0,
          right: 0,
          zIndex: 1,
        }}
      >
        <Slide in={!!message}>
          <Alert severity={severity} onClose={() => setMessage('')}>
            <AlertTitle>{severity.toUpperCase()}</AlertTitle>
            {message}
          </Alert>
        </Slide>
      </Box>

      <Backdrop
        open={isRunning}
        sx={{ color: 'primary.light', zIndex: (theme) => theme.zIndex.drawer + 1 }}
      >
        <CircularProgress color='inherit' />
      </Backdrop>

      {children}

    </AuthContext.Provider>
  );
}

export function useAuth() {
  return useContext(AuthContext);
}
