import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from './AuthContext';
import {
  Box, Button,
} from '@mui/material';
import {
  Logout as LogoutIcon,
} from '@mui/icons-material';

import SelectAppView from './children/SelectAppView';




function Main() {
  const [canAuth, setCanAuth] = useState(true);

  const navigation = useNavigate();

  const { isAuth, isRunning, setIsRunning, authSignOut, authVerifyJwt } = useAuth();



  // 画面が読み込まれたとき、１度（正確には２度？）だけ起動
  useEffect(() => {
    (async () => {
      setIsRunning(true);

      if (canAuth) {
        setCanAuth(false);
        // JWTで認証を確認
        await authVerifyJwt();
      }
      setIsRunning(false);
    })()
  }, []);

  // isAuthが変更されたとき、１度（正確には２度？）だけ起動
  useEffect(() => {
    // JWTで確認できなかったら、SignUpAndSignInへ移動
    if (!isAuth) {
      navigation('/');
    }
  }, [isAuth]);



  // 「サインアウト」ボタンが押されたときに起動
  const clickSignOut = async () => {
    setIsRunning(true);
    //サインアウト、SignUpAndSignInへ移動
    await authSignOut();
    navigation('/');
    setIsRunning(false);
  }


  return (
    <Box sx={{
      mt: 12,
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center'
    }}>

      <img src={`${process.env.PUBLIC_URL}/logo192.png`} alt='まいたけ帝国' />

      <Button
        onClick={clickSignOut}
        variant='outlined'
        disabled={isRunning}
        startIcon={<LogoutIcon />}>
        サインアウト
      </Button>

      <SelectAppView
        private={true}
      />

    </Box >
  );
}

export default Main;
